var header    = document.querySelector(".header");
var headroom  = new Headroom(header, {
    tolerance: 10,
});
headroom.init();

let navLinks = document.querySelector('.navigation__list');


window.addEventListener('scroll', (e) => {
    let lastActive = document.querySelectorAll(".navigation__item--active");
    
    lastActive[0] && lastActive[0].classList.remove("navigation__item--active");

    let i, titles = document.getElementsByTagName('h2');    

    for (i = 0; i < titles.length; i++) {
        let title = titles[i];
        if (title.getBoundingClientRect().top > 1) break;
    }

    if (--i >= 0) {
        let link = titles[i].firstElementChild && titles[i].firstElementChild.getAttribute("href"),
            currentActive = document.querySelector('.navigation__item[href="' + link + '"]');

        link && currentActive && currentActive.classList.add("navigation__item--active");
    }
})
window.addEventListener('scroll', (e) => {
    let header = document.querySelector(".header");
    
    header && header.classList.remove("header__bg");

    let secondSection = document.querySelector('#about');    

    if (secondSection.getBoundingClientRect().bottom < 1) {
        header && header.classList.add("header__bg");        
    }
    
})
var aboutSwiper = new Swiper ('.about__swiper', {
    direction: 'vertical',
    loop: true,
    pagination: {
        el: '.swiper-pagination',
        clickable: true,
        bulletClass: 'slider__dot',
        bulletActiveClass: 'slider__dot--active'
    },
})


var newsSwiper = new Swiper ('.news__swiper', {
    loop: true,
    slidesPerView: 'auto',
    centeredSlides: true,
    breakpoints: {
        768: {
            slidesPerView: 2,
            centeredSlides: false,
        },
        992: {
            slidesPerView: 3
        }
    },
    autoplay: {
        delay: 4000,
        disableOnInteraction: false,
    },
    spaceBetween: 30,
    pagination: {
        el: '.swiper-pagination',
        clickable: true,
        bulletClass: 'slider__dot',
        bulletActiveClass: 'slider__dot--active'
    },
    navigation: {
        nextEl: '.news__next',
        prevEl: '.news__prev',
    },
})
